<template lang="jade">
#filters
  a(href="#", @click.prevent="toggleFilters")
    h6.label--expand OPTIONS
    i.fa.fa-plus(v-show="!filterIsOpen")
    i.fa.fa-minus(v-show="filterIsOpen")
  div.menudrop(v-show="filterIsOpen")
    span Sort by:
    select(id="sortby-select", @change='sort', v-model='sortBy')
      option(value="time") time
      option(value="result") result
    br
    span Highlight: &nbsp
    input(type="checkbox", name="passed", id="passed-checkbox", @change='sort', v-model='highlightPassed')
    label.input-label(for="passed-checkbox") Passed votes
    br
    label(class="no-bold") Select MLA:
    select(id="mla-select", @change='sort', v-model='mlaSelected')
      option(value="0") none
      option(v-for="name in sortedMlaNames" v-bind:value="name")  {{name}}, {{mlas[name].district}}
    div
      a(href="#", class="text--small" @click.prevent="clearFilters") Clear all filters

</template>

<script>
import _ from 'lodash'
import {drawBill} from './whipCanvas/billDrawer'// function(mlaSelected, highlightPassed, sortBy)

import {
  mlas
} from './mlas'
let sortedMlaNames = []
_(mlas).keys().sort().forEach(name => {
  sortedMlaNames.push(name)
})

export default {
  data() {
      return {
        mlaSelected:null,
        highlightPassed:false,
        sortBy: 'time',
        mlas,
        sortedMlaNames,
        filterIsOpen: false
      }
    },
    methods: {
      toggleFilters() {
        this.filterIsOpen = !this.filterIsOpen;
      },
      sort(){
        drawBill(this.mlaSelected, this.highlightPassed, this.sortBy)
      }
    }
}
</script>

<style lang='stylus'>
@import "../../styles/main"

#filters
  margin-bottom:-10px
  max-width:60%

select
  width:100%

.menudrop {
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
  border-style:dashed
  border-width:3px
  border-color:black
}

</style>



