<template lang="jade">
.legend
  label(@click="toggle")
    h6.label--expand MLA LEGEND   
      i.fa.fa-plus(v-show="!isOpen")
      i.fa.fa-minus(v-show="isOpen")
  ul.menudrop(v-show='isOpen').list--clean
    li
      div.circle-container
        div.full-circle
        span.circle-label BC Liberal
    li
      div.circle-container
        div.full-circle.full-circle--ndp
        span.circle-label BC NDP
    li
      div.circle-container
        div.full-circle.full-circle--green
        span.circle-label BC Greens
    li
      div.circle-container
        div.full-circle.full-circle--indie
        span.circle-label Independent
    li
      div.circle-container
        div.full-circle.full-circle--selected
        span.circle-label Selected

</template>

<script>
export default {
  data(){
    return {
      isOpen:true
    }
  },
  methods:{
    toggle(){
      this.isOpen = !this.isOpen
    },
  }
}
</script>

<style lang='stylus'>
.legend
  max-width:40%
  position:absolute
  right:60px

li, p, span, label{
  color:black
  background:rgba(255,255,255,.7)
  padding:0
  margin:0
}

ul {
  margin-bottom:0
  padding-bottom:0
}


.list--clean {
  list-style: none;
  padding: 0;
}
.list--clean > li {
  list-style: none;
  display: inline;
}
.full-circle {
    background-color: rgba(255,0,0,1);
    height: 8px;
    border-radius: 50%;
    width: 8px;
    display: inline-block;
}
.full-circle--ndp {
    background-color: rgba(255,200,0,1);
}
.full-circle--green {
    background-color: rgba(0,255,0,1);
}
.full-circle--indie {
    background-color: rgba(125,125,125,1);
}
.full-circle--selected {
    background-color: rgba(0,0,0,1);
}
.menudrop {
  position: absolute;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
  border-style:dashed
  border-width:3px
  border-color:black
}


</style>



