import _ from 'lodash'
import {
  mlas
} from '../mlas'
import {
  rawbillsSorted,
  ynSorted
} from './processBills'

export const drawBill = (mlaSelected, highlightPassed, sortBy) => {
  var bills
  switch (sortBy) {
		default:
    case 'time':
      bills = rawbillsSorted
      break;
    case 'result':
      bills = ynSorted
      break;
  }

  const colors = {
    NDP: 'rgba(255,200,0,1)',
    Liberal: 'rgba(255,0,0,1)',
    Green: 'rgba(0,255,0,1)',
    Independent: 'rgba(125,125,125,1)',
    Selected: 'rgba(0,0,0,1)'
  }
  const fadedColors = {
    NDP: 'rgba(255,200,0,0.65)',
    Liberal: 'rgba(255,0,0,0.65)',
    Green: 'rgba(0,255,0,0.65)',
    Independent: 'rgba(125,125,125,0.65)',
    Selected: 'rgba(0,0,0,1)'
  }
  const canvas = document.getElementById('canvas')
  const ctx = canvas.getContext('2d')
  const width = window.innerWidth
  const height = window.innerHeight
  const v_center = height / 2
  const margin = 50;
  var scale = null;
  for (var i = 0; i < bills.length; i++) {
    if (scale < bills[i].yays.length) scale = bills[i].yays.length;
    if (scale < bills[i].nays.length) scale = bills[i].nays.length;
  }
  const h_interval = ((width - margin) / bills.length);
  const v_interval = (((height - margin) / (scale * 4.1)));
  const devicePixelRatio = window.devicePixelRatio || 1;
  const backingStoreRatio = ctx.webkitBackingStorePixelRatio || ctx.mozBackingStorePixelRatio || ctx.msBackingStorePixelRatio || ctx.oBackingStorePixelRatio || ctx.backingStorePixelRatio || 1;
  const ratio = devicePixelRatio / backingStoreRatio;

  function grid() {
    ctx.beginPath();
    ctx.fillStyle = 'white';
    ctx.fillRect(0, 0, width, height);
    ctx.closePath();
    ctx.beginPath();
    ctx.fillStyle = 'grey';
    for (var x = margin / 2; x < (width - (margin / 2)); x += h_interval) {
      for (var y = margin / 2; y < (height - (margin / 2)); y += v_interval) {
        ctx.fillRect(x, y, 1, 1);
      }
    }
    ctx.closePath();
  }

  canvas.height = height
  canvas.width = width

  function frame() {
    ctx.beginPath()
    ctx.fillStyle = 'white'
    ctx.strokeStyle = 'black'
    ctx.lineWidth = 1
    ctx.strokeRect(margin / 2, margin / 2, width - margin, height - margin)
    ctx.closePath()
  }

  function centerLine() {
    ctx.beginPath()
    ctx.strokeStyle = 'black'
    ctx.lineWidth = 3
    ctx.lineCap = "circle"
    ctx.moveTo(margin / 2, v_center)
    ctx.lineTo(width - margin / 2, v_center)
    ctx.stroke()
    ctx.closePath()
  }

  function animate_dot(x, y, party) {
    var opacity = 0;
    var party_color = mlaSelected ? fadedColors[party] : colors[party];
    ctx.beginPath();
    ctx.fillStyle = party_color;
    ctx.arc(x, y, v_interval, 0, Math.PI * 2);
    ctx.fill();
    ctx.closePath();
  }

  function highlight(n){
    var sectionWidth = (n - margin / 2) / h_interval
    var billSection = Math.floor(sectionWidth)
    ctx.beginPath()
    ctx.fillStyle = 'rgba(150, 234, 52, .55)'
    let x = billSection * h_interval + margin / 2
    let y = margin / 2
    let w = h_interval
    let h = height - margin
    ctx.fillRect(x, y, w, h)
    ctx.closePath()
  }

  function plot_bill(bill, n) {
    var yaysLength = bill.yays.length;
    var naysLength = bill.nays.length;
    var billIsPassed = yaysLength > naysLength;
    var maxVoteCount = billIsPassed ? yaysLength : naysLength;

    if (highlightPassed & billIsPassed) {
      highlight(n)
    }

    for (var i = 0, y = v_interval + 5; i < maxVoteCount; i++) {
      var party = null;
      if (bill.yays[i]) {
        party = (mlaSelected === bill.yays[i][0]) ? 'Selected' : bill.yays[i][1];
        animate_dot(n, v_center - y, party);
      }
      if (bill.nays[i]) {
        party = (mlaSelected === bill.nays[i][0]) ? 'Selected' : bill.nays[i][1];
        animate_dot(n, v_center + y, party);
      }
      y += (v_interval * 2);
    }
  }

  function chart() {
    var count = bills.length;
    var i = 0;
    x = margin / 2 + (h_interval / 2)
    for (var i = 0, x = margin / 2 + (h_interval / 2); i < count; i++) {
      plot_bill(bills[i], x)
      x += h_interval
    }
  }

  function init(){
    grid()
    frame()
    chart()
    centerLine()
  }

  init()

  // highlighted cells // little imprecise?
  canvas.addEventListener('mousemove', function(e){
      var location = e.clientX
      let billSection = Math.floor((location - margin / 2) / h_interval)
      init()
      ctx.beginPath()
      ctx.fillStyle = 'rgba(0,0,0,.3)'
      let x = billSection * h_interval + margin / 2
      let y = margin / 2
      let w = h_interval
      let h = height - margin
      ctx.fillRect(x,y,w,h)
      ctx.closePath()
  })

}



