import _ from 'lodash'

const mutations = {
  setSelectedMla(visualizeVotes, selected){
    visualizeVotes.status.mlaSelected = selected
  },
  setShowPassed(visualizeVotes, isShowPassed){
    visualizeVotes.showPassed = isShowPassed
  },
  setSortBy(visualizeVotes, sortBy){
    visualizeVotes.sortBy = sortBy
  },
}

const state = {
  status: {
    mlaSelected:null,
    showPassed: false,
    sortBy: 'result',
  }
}

export default {
  state,
  mutations,
}



